.field-container {
  display: flex;
  flex-direction: column;

  label,
  input,
  div.p-dropdown.p-component.mdb-select {
    width: 100% !important;
  }
}

.horizontal-padding {
  margin: 0 15px;
}

.how-to-use {
  display: flex;
  font-family: "Karla", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.29;
  letter-spacing: normal;
  text-align: justify;
  color: #5d6970;
  margin-top: 15px;

  button {
    color: #4dd9ca;
    font-size: 14px;
    font-weight: bold;
    margin-top: -3px;
    outline: none;
    border: none;
    cursor: pointer;
  }
}
