@import "fonts.scss";
@import "vars.scss";

html,
body,
div#root,
div.App {
  height: 100%;
  background-color: $area-background;
  font-family: "Poppins", sans-serif !important;
}

body .p-inputtext {
  border-radius: 5px;
  box-shadow: 2px 2px 6px -3px rgba(0, 0, 0, 0.8);
  border: solid 1px rgba(93, 105, 112, 0.3);
  background-color: #fff;
}

body .p-component-overlay {
  background-color: rgba(93, 105, 112, 0.7) !important;
}

html {
  font-size: 14px;
  color: #34363e;
  overflow: hidden;
}

.width-column-1 {
  width: 35%;
}

.width-column-2 {
  width: 30%;
}

.tag {
  border-radius: 5px;
  box-shadow: 4px 4px 11px -7px rgba(0, 0, 0, 0.8);
  border: solid 1px rgba(93, 105, 112, 0.3);
  padding: 0.5px 5px;
  margin: 1px;

  &__success {
    color: #00b06d;
  }

  &__error {
    color: #f25c63;
  }
  &__default {
    color: #2c263f;
  }
}

.minds-panel {
  display: inline-block;
  width: 100%;
  margin: 20px;
  text-align: left;
}

.minds-panel.p-panel .p-panel-content {
  padding: 20px;
  color: #33333a;
}

body .p-growl {
  top: unset !important;
}

body .p-component {
  font-family: "Karla", sans-serif;
}

body p {
  text-align: justify;
  font-family: "Karla", sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

.separate-btns {
  display: flex;
  justify-content: flex-start;
}

.loader-spinner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;

  i {
    font-size: 5em !important;
    color: $color-shamrock !important;
  }
}

.loader-bars {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;

  .message-loader {
    margin-top: -80px;

    span {
      margin-right: 5px;
    }
  }

  .bars {
    margin: 100px auto;
    width: 50px;
    height: 40px;
    text-align: center;
    font-size: 10px;

    div {
      background-color: $color-shamrock;
      height: 100%;
      width: 4px;
      display: inline-block;
      margin: 0 1px 0 1px;
      -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
      animation: sk-stretchdelay 1.2s infinite ease-in-out;
    }

    .rect2 {
      -webkit-animation-delay: -1.1s;
      animation-delay: -1.1s;
    }

    .rect3 {
      -webkit-animation-delay: -1s;
      animation-delay: -1s;
    }

    .rect4 {
      -webkit-animation-delay: -0.9s;
      animation-delay: -0.9s;
    }

    .rect5 {
      -webkit-animation-delay: -0.8s;
      animation-delay: -0.8s;
    }
  }
}

@-webkit-keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    -webkit-transform: scaleY(0.4);
  }

  20% {
    -webkit-transform: scaleY(1);
  }
}

@keyframes sk-stretchdelay {
  0%,
  40%,
  100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }

  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1);
  }
}

.minds-dialog {
  &.width-100 {
    .p-dialog-content {
      width: 100%;
    }
  }

  .p-dialog-content {
    div.minds-dialog__field {
      &:not(:first-child) {
        margin-top: 17px;
      }

      display: flex;
      flex-flow: column;
      align-items: flex-start;

      label {
        min-width: 120px;
        text-align: left;
      }

      div.p-dropdown {
        flex: 1;
      }
    }
  }

  .super-container {
    display: flex;

    .left-container {
      width: 100%;
      margin-right: 26px;
      position: relative;
    }

    .right-container {
      width: 100%;
      margin-left: 26px;
      margin-top: 20px;
    }
  }
}

body .p-message {
  &.p-message-success {
    color: $green-color !important;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    margin-left: -38px;
    background: #fff;
    border-left: transparent;

    .p-message-icon {
      color: $green-color !important;
      font-size: 18px !important;
      margin-right: 10px;
    }
  }

  &.p-message-error {
    background-color: #fce9ea !important;
    color: #e32b31 !important;
    display: flex;
    align-items: center;
    padding: 5px 20px;
    height: 40px;
    font-size: 14px !important;
    border-radius: 5px;

    .p-message-icon {
      color: #e32b31 !important;
      font-size: 18px !important;
      margin-right: 10px;
    }
  }
}

//General styles
.minds-panel {
  margin: 0;
}

.header {
  position: relative;
  z-index: 999999;

  &__title {
    display: flex;
    align-items: center;

    color: $color-text-dark;
    font-family: "Rubik Medium";
    font-size: 36px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
  }
}

.header,
.p-panel-titlebar {
  background-color: #fff; // !important;
  height: $header-height;
  display: flex;
  align-items: center;
  align-content: space-between;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 1.5em;
  color: '#2c263f';
  font-family: "Rubik Medium";
  font-weight: 300;
  border: none !important;
  border-bottom: solid 1px #f8f8f8 !important;

  .header-status {
    border-radius: 5px;
    padding: 5px 20px;
    font-size: 0.8em;
    display: flex;
    align-content: center;
    align-items: center;

    svg {
      width: 30px;
      height: 30px;
      margin-right: 20px;
    }

    &.warning {
      background-color: #faf6e7;
      color: #d5b52e;

      svg {
        fill: #d5b52e;
      }
    }

    &.info {
      background-color: #e7f5ff;

      svg {
        fill: #08265a;
      }
    }
  }
}

.main-content > .header {
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;

  & > * {
    flex: 1;
  }
}

.breadcrumbs {
  color: #939ea4;
  display: flex;
  align-items: center;

  .breadcrumb-link {
    text-decoration: none;
    color: #939ea4;
    cursor: pointer;

    &:hover {
      color: $color-shamrock;
    }

    transition: color 0.2s;
  }

  .breadcrumb-item {
    display: inline-block;

    font-family: Karla;
    font-size: 16px;
    line-height: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;

    margin-right: 20px;

    &:not(:first-child) {
      &::before {
        content: ">";
        margin-right: 20px;
      }
    }
  }
}

.card {
  &:not(.no-shadow) {
    box-shadow: 0px 9px 18px 0 rgba(0, 0, 0, 0.03);
  }
}

.card,
.p-toggleable-content {
  margin: 20px;
  background-color: #fff;
  border-radius: 5px;

  .header {
    height: 75px;
  }

  .content,
  .p-panel-content {
    padding: 20px;
    border: none !important;
  }
}

.card-with-summary {
  display: flex;
  align-items: flex-start;

  .card {
    flex: 1;
    margin-right: 0;
  }
}

.p-toggleable-content {
  background-color: transparent;
}

.input-block {
  margin-bottom: 20px;

  .input-title {
    margin-right: 30px;
  }

  input {
    height: 40px;
    border-radius: 5px;
    border: solid 1px #e1e1e1;
    background-color: #ffffff;
    padding: 10px;
    font-size: 1em;
    color: #34363e;
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 0;
      border-color: rgba(42, 182, 115, 0.8);
      box-shadow: $focus-shadow;
    }

    &:disabled {
      color: #c0c0c0;
    }
  }
}

body .mdb-input,
body .p-component.mdb-input {
  @include font-text-main;
  font-size: 14px;

  height: 40px;
  border-radius: 5px;
  border: solid 1px #e1e1e1;
  background-color: #ffffff;
  padding: 10px;
  color: #34363e;
  transition: all 0.3s ease-in-out !important;

  &:focus,
  &:hover {
    outline: 0 !important;
    border-color: rgba(42, 182, 115, 0.8) !important;
    box-shadow: $focus-shadow !important;
  }

  &:disabled {
    color: #c0c0c0 !important;
  }
}

$input-placeholder-color: #bbb;

input.p-inputtext.mdb-input {
  &:not(.p-disabled) {
    border-color: #e1e1e1;
  }

  &::placeholder {
    @include font-text-main;
    font-size: 14px;
    padding-left: 5px;
    color: #5d6970;
  }

  &.p-disabled {
    border-color: #ddd;
  }

  &.p-disabled::placeholder {
    color: #ddd;
  }

  &.error {
    border-color: #f25c63 !important;
  }
}

.p-dropdown.mdb-select,
.p-multiselect.mdb-select {
  height: 29px;
  width: 316px;
  border-radius: 5px;
  border: solid 1px rgba(93, 105, 112, 0.3) !important;
  box-shadow: 2px 2px 6px -3px rgba(0, 0, 0, 0.8);
  background-color: #ffffff;
  padding: 5px 10px;

  label.p-dropdown-label,
  .p-dropdown-item {
    color: #2c263f !important;
    box-shadow: none;
    @include font-text-main;
    font-size: 14px;
    padding-left: 0px;
    background: transparent !important;
    margin-top: -7px;

    &.p-placeholder {
      color: #5d6970 !important;
    }
  }

  font-size: 1em;
  color: #34363e;
  transition: all 0.3s ease-in-out;

  .p-dropdown-trigger,
  .p-multiselect-trigger {
    color: #e1e1e1;
  }

  &:not(.p-disabled):hover,
  &:not(.p-disabled):focus,
  &:not(.p-disabled):active,
  &:not(.p-disabled).p-focus {
    outline: 0 !important;
    border-color: rgba(42, 182, 115, 0.8) !important;
    box-shadow: $focus-shadow !important;
  }

  &:disabled {
    color: #c0c0c0 !important;
  }

  .p-dropdown-panel .p-dropdown-items .p-dropdown-item.p-highlight,
  .p-multiselect-item.p-highlight {
    background-color: $green-color !important;

    &:focus {
      box-shadow: none !important;
    }
  }

  .p-multiselect-filter-icon {
    color: $green-color !important;
  }

  .p-checkbox-box.p-highlight {
    background-color: $green-color !important;
    border-color: #fff !important;
  }

  .p-multiselect-item {
    &:focus {
      box-shadow: none !important;
    }
  }

  .p-multiselect-item:not(.p-highlight):not(.p-disabled):hover {
    background-color: rgba(42, 182, 115, 0.3) !important;
  }
}

.p-checkbox {
  .p-checkbox-box {
    width: 22px;
    height: 21px;
    background-color: #ffffff;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: #ffffff;

    &.p-highlight {
      background-color: $green-color !important;
      border-color: #fff !important;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      text-align: center;
      color: $green-color;
    }
  }

  &.use-delete-icon .p-checkbox-box {
    &.p-highlight {
      background-color: $color-watermelon !important;
      border-radius: 10px;

      .p-checkbox-icon.pi-check:before {
        content: "\e90f";
      }
    }
  }
}

.p-checkbox-label {
  margin: 0 !important;
  margin-bottom: 10px !important;
  font-family: "Rubik Medium";
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #2c263f;
}

.outline-btn-green {
  height: 40px !important;
  border-radius: 5px !important;
  border: solid 1px $green-color !important;
  background-color: transparent !important;
  transition: all ease 0.3s !important;
  cursor: pointer !important;
  font-size: 1em !important;
  padding: 5px 20px !important;
  font-weight: normal !important;
  font-style: normal !important;
  font-stretch: normal !important;
  letter-spacing: normal !important;
  color: $green-color !important;

  &:hover {
    color: white !important;
    background-color: $green-color !important;

    svg {
      fill: #fff;
    }
  }

  &:active,
  &:focus {
    outline: 0 !important;
    box-shadow: none !important;
  }

  svg {
    width: 14px;
    height: 14px;
    fill: $green-color;
  }

  &:disabled {
    color: lighten($green-color, 30%) !important;
    border-color: lighten($green-color, 30%) !important;

    &:hover {
      cursor: defatult !important;
      background-color: transparent !important;
      color: lighten($green-color, 30%) !important;

      svg {
        fill: lighten($green-color, 30%);
      }
    }
  }
}

button.p-button.minds-action-btn,
button.p-button.p-button-text-only.minds-action-btn {
  &.lg-btn {
    width: auto;
    height: 32px;
    margin-right: -10px;
  }

  &.lg-btn-spacing {
    width: auto;
    height: 32px;
  }

  &.sm-btn {
    margin-right: -10px;
  }

  &.sm-btn {
    margin-right: -10px;
  }

  &.abs-btn-position {
    position: absolute;
    bottom: -1px;
    right: 0px;
  }

  height: 32px;
  border-radius: 7px;
  background-color: $color-shamrock;
  border-width: 0px;

  &:not(:first-child) {
    margin-left: 1.5rem;
  }

  &:hover {
    background-color: darken($color-shamrock, 10);
  }

  &.bold {
    font-weight: bold !important;
  }

  &.red {
    background-color: $color-watermelon;
  }

  &.red:hover {
    background-color: darken($color-watermelon, 10); // adjust-hue($color-watermelon, 0deg)
  }

  &.yellow {
    background-color: $color-yellow;
  }

  &.yellow:hover {
    background-color: darken($color-yellow, 10); // adjust-hue($color-watermelon, 0deg)
  }

  &[disabled] {
    background-color: #939ea4;
  }

  .p-button-text {
    padding: 0 17px;
    word-break: keep-all;
    font-family: "Rubik Medium";
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;

    .icon-text {
      i {
        margin-right: 0.5em;
      }
    }

    .minds-action-btn__icon {
      font-size: 12px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      padding: 0px;
      width: 12px;
    }
  }
}

.action-btn {
  height: 30px;
  width: 30px;
  border: none !important;
  border-radius: 5px;
  margin-left: 5px;
  margin-right: 5px;
  cursor: pointer !important;
  transition: all ease 0.3s;
  display: flex !important;
  align-content: center !important;
  align-items: center !important;

  .p-button-text {
    padding: 0 !important;
    width: 100%;
  }

  &:hover {
    svg {
      fill: #fff !important;
    }
  }

  svg {
    transition: all ease 0.3s;
    height: 14px;
    width: 14px;
  }

  &.download {
    background-color: #eaf8f1;

    svg {
      fill: $green-color;
    }

    &:hover {
      background-color: $green-color !important;
    }
  }

  &.search {
    background-color: #faf6e7;

    svg {
      fill: #d5b52e;
    }

    &:hover {
      background-color: #d5b52e !important;
    }
  }

  &.delete {
    background-color: #fce9ea;

    svg {
      fill: #e32b31;
    }

    &:hover {
      background-color: #e32b31 !important;
    }
  }

  &.circleQuestion {
    background-color: #e7f5ff;

    svg {
      width: 18px;
      height: 18px;
      fill: #08265a;
    }

    &:hover {
      background-color: #08265a !important;
    }
  }

  &:disabled {
    background-color: #f4f4f4;

    svg {
      fill: #33333a !important;
    }

    &:hover {
      background-color: #f4f4f4 !important;

      svg {
        fill: #33333a !important;
      }
    }
  }
}

.p-dialog {
  border: none !important;
  border-radius: 5px !important;
  top: calc(50% + 40px) !important;
  left: calc(50% + 125px) !important;

  .p-dialog-titlebar {
    font-family: "Rubik Medium";
    background-color: #fff !important;
    border: none !important;
    border-top-right-radius: 5px !important;
    border-top-left-radius: 5px !important;
    padding: 22px 25px !important;
    font-size: 36px;
    font-weight: 500;

    span {
      font-weight: 500 !important;
    }

    .p-dialog-titlebar-icons {
      .p-link:focus {
        box-shadow: none;
      }
    }
  }

  .p-dialog-content {
    width: 100%;
    padding: 7px 30px 23px 30px !important;
    border: none !important;
    border-bottom-right-radius: 5px !important;
    border-bottom-left-radius: 5px !important;

    .minds-inputgroup {
      margin-top: 20px;
      display: flex;
      align-items: center;
      align-content: flex-end;
      height: 60px;

      .minds-file-input-value {
        left: 80px;
      }

      .minds-inputgroup-addon {
        border: none;
        background-color: #fff;
        color: #2c263f;
        padding: 0;
        margin-right: 20px;
        text-align: left;
      }
    }

    input {
      border: solid 1px rgba(93, 105, 112, 0.3) !important;
      box-shadow: 2px 2px 6px -3px rgba(0, 0, 0, 0.8);
      border-radius: 5px !important;
      height: 29px;
      width: 316px;
      flex: 1;

      &:focus {
        outline: 0 !important;
        border-color: rgba(42, 182, 115, 0.8) !important;
        box-shadow: $focus-shadow !important;
      }

      &.inputError {
        border-color: rgba(227, 43, 49, 0.8) !important;
        box-shadow: 0 0 10px rgba(227, 43, 49, 0.8) !important;
      }

      &::placeholder {
        color: #5d6970;
        padding: 0 !important;
      }
    }

    .mdb-input {
      flex: 1;
    }

    .separate-btns {
      margin-top: 60px;
    }
  }
}

body {
  .p-accordion {
    margin-top: 15px;
    margin-bottom: 20px;

    .p-accordion-tab:not(:last-child) {
      margin-bottom: 15px;
    }

    .p-accordion-header {
      a {
        &:hover {
          border: solid 1px #c8c8c8;
        }

        background-color: #fff !important;

        .p-accordion-toggle-icon {
          float: right;
        }

        &:not(.p-disabled) {
          &:focus {
            box-shadow: none !important;
          }
        }
      }

      &.p-highlight {
        border: 1px solid #c8c8c8;
        border-radius: 5px;
        border-bottom: 0;
        margin-bottom: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;

        &:not(.p-disabled) {
          a {
            border-color: #fff !important;
            color: #222 !important;
          }
        }
      }
    }

    .p-toggleable-content-enter-done,
    .p-toggleable-content-enter-active,
    .p-toggleable-content-exit-active {
      border: 1px solid #c8c8c8;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-top: 0;
      margin: 0;

      .p-accordion-content {
        border: 0;
        padding: 20px;
      }
    }
  }
}

// +++ minds-table
// div.p-component.p-datatable.minds-table div.p-datatable-wrapper table
div.p-component.p-datatable.minds-table {
  .p-datatable-wrapper {
    padding-bottom: 20px;
    background-color: white;
    width: fit-content;
    min-width: 100%;
  }

  table {
    table-layout: auto;

    thead.p-datatable-thead tr {
      height: 70px;
    }

    thead.p-datatable-thead tr th {
      background-color: transparent;
      border: none;

      vertical-align: middle;

      font-family: SFMono;
      font-size: 14px;
      font-weight: 700 !important;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: #2c263f;

      .p-sortable-column-icon.pi.pi-fw {
        color: #34363e !important;
      }

      &:first-child {
        padding-left: 20px;
        text-align: left;
        border-top-left-radius: 5px;
      }

      &:last-child {
        padding-right: 20px;
      }

      &.p-sortable-column:hover {
        background-color: #eaeaea;
      }

      box-shadow: none; // disable selection box
    }

    tbody.p-datatable-tbody tr {
      outline: none; // disable selection box

      &:nth-child(even) {
        background-color: white;
      }

      &:nth-child(odd) {
        background-color: white;
      }

      div.minds-table__actions {
        display: flex;
        justify-content: center;
      }

      td {
        border: none;

        height: 42px;
        font-family: SFMono;
        font-size: 12px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;

        &:first-child {
          padding-left: 20px;
          text-align: left;

          color: #2c263f;
          font-family: SFMono;
          font-size: 14px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-transform: uppercase;
        }

        &:last-child {
          padding-right: 20px;
        }
      }
    }
  }
}

// stripped mod
div.p-component.p-datatable.minds-table.stripped table tbody.p-datatable-tbody tr {
  &:nth-child(even) {
    background-color: #f3f3f4;
  }

  &:nth-child(odd) {
    background-color: white;
  }
}

div.p-component.p-datatable.minds-table.card {
  .p-datatable-wrapper {
    box-shadow: $panel-shadow;
    border-radius: 5px;
    overflow: scroll;
    overflow-y: auto;
    overflow-x: auto;
    position: relative;
    z-index: 10;

    &::-webkit-scrollbar {
      width: 10px;
      background-color: #fafafa;
      border-radius: 2px;
      border: solid 1px #ebebeb;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #c2c2c2;
      border-radius: 5px;
      border: 2px solid #fafafa;
    }
  }

  .p-paginator.minds-paginator {
    position: relative;
    z-index: 5;
  }
}

// ---------------

.main-content {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__body {
    overflow: auto;
    flex: 1;
  }
}

.main-content {
  .fixed-buttons {
    position: fixed;
    z-index: 100;
    bottom: 2.5rem;
    right: 2rem;
  }
}

// .main-content__body
.table-fit-scroll {
  // fixed header
  $main-content-padding: 20px;

  .p-datatable.minds-table {
    // height: 100%;
    display: grid;
    grid-template-rows: 1fr auto;
    grid-template-columns: 1fr;
    min-height: 0;
    max-height: calc(100% - #{$main-content-padding} * 2);
    background: transparent;
    box-shadow: none;

    .p-datatable-outer-wrapper {
      background-color: white;
      min-width: fit-content;
      display: block;
      position: relative;
      z-index: 10;
      min-height: 0;
      border-radius: 5px;
      box-shadow: 4px 4px 11px -7px rgba(0, 0, 0, 0.8);
      border: solid 1px rgba(93, 105, 112, 0.3);

      .p-datatable-wrapper {
        max-height: 100%;
        min-height: 0;
        overflow-y: auto;
        position: static !important;
        box-shadow: none !important;

        table thead {
          display: block;
          position: absolute;
          top: 0;
        }
      }
    }
  }
}

.fas {
  margin-left: 4px;
  font-size: 12px;
}

.empty-page-container {
  color: #626f75;
  background-color: white;
  margin: 10px auto;
  width: 98%;
  height: calc(100% - -50px);

  .message {
    display: block;
    position: relative;
    top: 25%;

    strong {
      margin: 0 auto;
      display: block;
      text-align: center;
      padding: 30px;
      font-size: 14px;
      font-weight: 400;
      font-family: Karla;
      font-weight: bold;

      &.text-width {
        width: 34%;
      }

      &:before {
        border-bottom-color: green;
      }
    }

    .more-information i {
      color: #fedc8c;
      font-size: 12px;
  }

    i {
      display: block;
      font-size: 132px;
      text-align: center;
      color: #5c6a70;
    }
  }
}

/* Create two equal columns that floats next to each other */
.column {
  float: left;
  width: 50%;
  padding: 10px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.flex-container {
  display: flex;
}

@import "./components/layout/utils/grid.scss";
