$color-error: #FFB8B3;
$color-warning: #FFFECD;
$color-info: #BFEFFF;

$color-shamrock: #00b06d;
$color-light-shamrock: #73d4af;
$color-text: #939ea4;
$color-text-dark: #2c263f;
$color-watermelon: #f25c63;
$color-yellow: #eecc58;

$green-color: #26b873;
$green-color-faded: #26b8736b;

$area-background: #f8f8f8;

$header-height: 110px;

$panel-shadow: 4px 4px 10px -7px rgba(0, 0, 0, 1.0);

$focus-shadow: 0 0 10px rgba(42, 182, 115, 0.8);

@mixin font-chart-legend {
    font-family: SFMono;
    font-size: 10px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    // line-height: 1;
    letter-spacing: normal;
    color: $color-text-dark;
}

@mixin font-title-small {
    font-family: 'Rubik Medium';
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    color: $color-text-dark;
}

@mixin font-text-main {
    font-family: Karla;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: $color-text-dark;
}
