/* poppins-100 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 100;
  src: local('Poppins Thin'), local('Poppins-Thin'),
       url('assets/fonts/poppins/poppins-v6-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
       url('assets/fonts/poppins/poppins-v6-latin-100.woff') format('woff'), /* Modern Browsers */
       url('assets/fonts/poppins/poppins-v6-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
       url('assets/fonts/poppins/poppins-v6-latin-100.svg#Poppins') format('svg'); /* Legacy iOS */
}
