.updater {
  // width: 50rem;
  height: 50px;
  background: rgba(255, 255, 255, 0.8);

  position: absolute;
  z-index: 1000;
  bottom: 1vh;
  right: 1vw;

  display: flex;
  align-items: center;

  padding: 2rem;
  box-shadow: 1px 1px 5px -2px black;
  border-radius: 5px;

  .updater__closebtn.p-button.p-button-icon-only {
    position: absolute;
    top: 0px;
    right: 0px;
    border-width: 0px;
    background-color: transparent;
    &:hover {
      background-color: transparent;
      color: steelblue;
    }
    &:focus {
      box-shadow: 0px 0px 1px steelblue;
    }
    .p-button-text {
      padding: 0px;
    }
  }
}

.separate-btns {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;

  &.move-right {
    justify-content: flex-end;
  }

  .mode-button {
    font-family: 'Karla', sans-serif;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: 0.28px;
    text-align: center;
    color: #4dd9ca;
    margin-top: 6px;
    cursor: pointer;
  }
}
